"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { doc, getDoc } from "firebase/firestore"
import { db } from "@/lib/firebaseClient"
import { useAuth } from "@/lib/auth-context"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface PlayerProfile {
  name?: string
  email?: string
  ageRange?: string
  gender?: string
  sports?: string[]
  onboardingCompleted?: boolean
}

export default function PlayerProfilePage() {
  const router = useRouter()
  const { user, loading } = useAuth()
  const [profile, setProfile] = useState<PlayerProfile | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    if (!loading && !user) {
      router.replace("/players/login")
      return
    }

    if (!loading && user) {
      const fetchProfile = async () => {
        try {
          const profileDoc = await getDoc(doc(db, "players", user.uid))
          if (profileDoc.exists()) {
            setProfile(profileDoc.data() as PlayerProfile)
          } else {
            setProfile(null)
          }
        } catch (error) {
          console.error("[PlayersProfile] Error loading profile:", error)
          setProfile(null)
        } finally {
          setIsLoading(false)
        }
      }

      fetchProfile()
    }
  }, [loading, user, router])

  return (
    <main className="min-h-screen bg-background flex items-center justify-center p-6">
      <Card className="w-full max-w-2xl shadow-xl border-0">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl text-gray-900">Mi perfil de jugador</CardTitle>
          <CardDescription className="text-gray-600">
            Tus preferencias y datos de juego
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {isLoading ? (
            <p className="text-gray-600 text-center">Cargando perfil...</p>
          ) : profile ? (
            <div className="space-y-4 text-gray-900">
              <div>
                <p className="text-sm text-gray-500">Nombre</p>
                <p className="text-base font-medium">{profile.name || user?.displayName || "—"}</p>
              </div>
              <div>
                <p className="text-sm text-gray-500">Email</p>
                <p className="text-base font-medium">{profile.email || user?.email || "—"}</p>
              </div>
              <div>
                <p className="text-sm text-gray-500">Rango de edad</p>
                <p className="text-base font-medium">{profile.ageRange || "—"}</p>
              </div>
              <div>
                <p className="text-sm text-gray-500">Género</p>
                <p className="text-base font-medium">{profile.gender || "—"}</p>
              </div>
              <div>
                <p className="text-sm text-gray-500">Deportes</p>
                <p className="text-base font-medium">
                  {profile.sports?.length ? profile.sports.join(", ") : "—"}
                </p>
              </div>
            </div>
          ) : (
            <p className="text-gray-600 text-center">No encontramos tu perfil todavía.</p>
          )}

          <div className="flex justify-center">
            <Button onClick={() => router.push("/players")} className="rounded-full px-8">
              Ir a mi dashboard
            </Button>
          </div>
        </CardContent>
      </Card>
    </main>
  )
}
